import React, { useEffect, useState } from 'react';
// import truthAndDareData from './truthAndDareData.json';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import Modal from '@mui/material/Modal';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardHeader from '@mui/material/CardHeader';
import DeleteIcon from '@mui/icons-material/Delete';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Slider from '@mui/material/Slider';
import PersonAddAltIcon from '@mui/icons-material/PersonAddAlt';
// import BottomNavigation from '@mui/material/BottomNavigation';
// import BottomNavigationAction from '@mui/material/BottomNavigationAction';
// import CachedIcon from '@mui/icons-material/Cached';
import CloseIcon from '@mui/icons-material/Close';
import { Wheel } from 'react-custom-roulette';


const PlayerList = () => {
    const [players, setPlayers] = useState([]);
    const [randomPlayer, setRandomPlayer] = useState(null);
    const [taskType, setTaskType] = useState('');
    const [randomLabel, setRandomLabel] = useState(0);
    const [gender, setGender] = useState('Male');
    const [showPopup, setShowPopup] = useState(false);
    const [sliderValue, setSliderValue] = useState(2);
    const [truthAndDareData, setJsonData] = useState(null);
    const [mustSpin, setMustSpin] = useState(false);
    const [prizeNumber, setPrizeNumber] = useState(0);
    const [spinPlayer, setspinPlayer] = useState('');
    const [showBlackBox, setShowBlackBox] = useState(false);

    const handleStopSpinning = () => {
        setMustSpin(false);
        setShowBlackBox(true);
      };

    const fetchData = async () => {
        try {
            const response = await fetch('/truth-dare/truthAndDareData.json');
            const data = await response.json();
            setJsonData(data);
        } catch (error) {
            console.error('Error fetching JSON data:', error);
        }
    };

    useEffect(() => {
        fetchData();
    }, []);


    const handleAddPlayer = (event) => {
        event.preventDefault();
        const playerName = event.target.elements.playerName.value;
        const r = Math.floor(Math.random() * 256);
        const g = Math.floor(Math.random() * 256);
        const b = Math.floor(Math.random() * 256);
        if (playerName && gender) {
            const PlayerColor = { option: playerName, style: { backgroundColor: `rgb(${r}, ${g}, ${b})` } };
            const newPlayer = { name: playerName, gender };
            setPlayers([...players, newPlayer]);
            setspinPlayer([...spinPlayer, PlayerColor]);
            event.target.reset();
        }
    };

    const handleDeletePlayer = (index) => {
        const updatedPlayers = [...players];
        updatedPlayers.splice(index, 1);
        setPlayers(updatedPlayers);
    };

    const handleStartGame = (event) => {
        if (!mustSpin) {
            event.preventDefault();
            const newPrizeNumber = Math.floor(Math.random() * players.length);
            // const randomIndex = Math.floor(Math.random() * players.length);
            const randomPlayerLabel = randomLabel;
            setPrizeNumber(newPrizeNumber);
            setRandomPlayer(players[newPrizeNumber]);
            setTaskType('');
            setRandomLabel(randomPlayerLabel);
            setMustSpin(true);
            setShowBlackBox(false);
        }
        // event.preventDefault();
        // const randomIndex = Math.floor(Math.random() * players.length);
        // const randomPlayerLabel = randomLabel;
        // setRandomPlayer(players[randomIndex]);
        // setTaskType('');
        // setRandomLabel(randomPlayerLabel);
        // console.log(spinPlayer);
    };

    const StartGame = (event) => {
        event.preventDefault();
        const randomIndex = Math.floor(Math.random() * players.length);

        const randomPlayerLabel = parseInt(sliderValue);
        setRandomPlayer(players[randomIndex]);
        setTaskType('');
        setRandomLabel(randomPlayerLabel);
        setTaskType('');
        setShowPopup(true);
    };

    const handleSelectTaskType = (type) => {
        setTaskType(type);
    };

    const handleGetTaskText = () => {
        if (taskType === 'truth') {
            const tasks = truthAndDareData.truth.filter(
                (task) =>
                    task.label === randomLabel &&
                    (task.gender === '' || task.gender.toLowerCase() === randomPlayer.gender.toLowerCase())
            );
            if (tasks.length > 0) {
                const randomIndex = Math.floor(Math.random() * tasks.length);
                return tasks[randomIndex].text;
            }
        } else if (taskType === 'dare') {
            let tasks = [];
            if (randomPlayer.gender.toLowerCase() === 'female') {
                tasks = truthAndDareData.dare.filter(
                    (task) => task.label === randomLabel && (task.gender === '' || task.gender.toLowerCase() === 'female')
                );
            } else if (randomPlayer.gender.toLowerCase() === 'male') {
                tasks = truthAndDareData.dare.filter(
                    (task) => task.label === randomLabel && (task.gender === '' || task.gender.toLowerCase() === 'male')
                );
            }
            if (tasks.length > 0) {
                const randomIndex = Math.floor(Math.random() * tasks.length);
                return tasks[randomIndex].text;
            }
        }
        return '';
    };

    const handleGenderChange = (event) => {
        setGender(event.target.value);
    };

    const handleClosePopup = () => {
        setShowPopup(false);
    };

    const handleSliderChange = (event, value) => {
        setSliderValue(value);
    };


    return (
        <div>
            <div style={{ display: 'flex', justifyContent: 'space-between', flexWrap: 'wrap' }}>
                <Card sx={{ bgcolor: 'white', color: 'gray', width: '100%', marginBottom: '10px', '@media (min-width: 768px)': { width: 'calc(50% - 10px)', marginBottom: '0' } }}>
                    <CardHeader title="Add Players" sx={{ bgcolor: 'primary.dark', color: 'white' }} />
                    <CardContent>
                        <Box sx={{ display: 'flex', justifyContent: 'center', pb: 8 }}>
                            <form onSubmit={handleAddPlayer} style={{ display: 'flex', alignItems: 'center' }}>
                                <TextField
                                    id="outlined-basic"
                                    label="Player Name"
                                    variant="outlined"
                                    name="playerName"
                                    placeholder="Enter player name"
                                    sx={{ width: '120px', marginRight: '10px' }} // Adjust the width as needed
                                />
                                <FormControl variant="outlined" sx={{ width: '100px' }}>
                                    <InputLabel id="demo-simple-select-label">Gender</InputLabel>
                                    <Select
                                        labelId="demo-simple-select-label"
                                        id="demo-simple-select"
                                        value={gender}
                                        onChange={handleGenderChange}
                                        label="Gender"
                                    >
                                        <MenuItem value="Male">Male</MenuItem>
                                        <MenuItem value="Female">Female</MenuItem>
                                    </Select>
                                </FormControl>
                                <Button type="submit" variant="contained" sx={{ minWidth: '50px' }}>
                                    <PersonAddAltIcon />
                                </Button>
                            </form>
                        </Box>

                        <Box sx={{ display: 'flex', justifyContent: 'center', pb: 8 }}>
                            {players.length > 0 && (
                                <form onSubmit={StartGame}>
                                    <Slider
                                        aria-label="Temperature"
                                        value={sliderValue} // Use the state variable for value
                                        valueLabelDisplay="auto"
                                        step={1}
                                        marks
                                        min={1}
                                        max={4}
                                        onChange={handleSliderChange} // Add the onChange handler
                                    />
                                    <Button type="submit" variant="contained">
                                        Start Game
                                    </Button>
                                </form>
                            )}
                        </Box>
                    </CardContent>
                </Card>
                <Card sx={{ bgcolor: 'white', color: 'gray', width: '100%', '@media (min-width: 768px)': { width: 'calc(50% - 10px)' } }}>
                    <CardHeader title="Players List" sx={{ bgcolor: 'primary.dark', color: 'white' }} />
                    <CardContent>
                        {showPopup && randomPlayer && (
                            <Modal open={showPopup} onClose={handleClosePopup} className="center">
                                <Card sx={{ position: 'absolute', top: '0', left: '0', right: '0', bottom: '0', margin: 'auto' }}>

                                    <CardHeader
                                        title={`Truth & Dare`}
                                        sx={{ bgcolor: 'primary.dark', color: 'white' }}
                                        action={

                                            <CloseIcon onClick={handleClosePopup} />
                                        }
                                    />
                                    <CardContent>
                                        {/* <Box sx={{ display: 'flex', justifyContent: 'center', pb: 1 }}>
                                            <Typography variant="h6" component="h6">
                                                {showBlackBox && ( `Now It's  ${randomPlayer.name} 's turn.`)}
                                            </Typography>
                                        </Box> */}

                                        <div style={{ position: 'fixed', bottom: 0, left: 0, width: '100%' }}>
                                            <div style={{ display: 'flex' }}>
                                                {/* Truth button */}
                                                <Button
                                                    onClick={() => handleSelectTaskType('truth')}
                                                    disabled={taskType === 'dare'}
                                                    variant="contained"
                                                    sx={{
                                                        flex: '1',
                                                        marginRight: '10px',
                                                        color: 'white',
                                                        backgroundColor: taskType === 'truth' ? '#e57373' : '#f44336',
                                                        '&:hover': {
                                                            backgroundColor: taskType === 'truth' ? '#ef5350' : '#e53935',
                                                        },
                                                    }}
                                                >
                                                    Truth
                                                </Button>
                                                {/* Dare button */}
                                                <Button
                                                    onClick={() => handleSelectTaskType('dare')}
                                                    disabled={taskType === 'truth'}
                                                    variant="contained"
                                                    sx={{
                                                        flex: '1',
                                                        color: 'white',
                                                        backgroundColor: taskType === 'dare' ? '#7986cb' : '#3f51b5',
                                                        '&:hover': {
                                                            backgroundColor: taskType === 'dare' ? '#5c6bc0' : '#3949ab',
                                                        },
                                                    }}
                                                >
                                                    Dare
                                                </Button>
                                            </div>
                                        </div>

                                        {/* Task display */}
                                        <Box sx={{ mt: 2, textAlign: 'center' }}>
                                            {taskType && (
                                                <Typography variant="h5">
                                                    {taskType}: {handleGetTaskText()}
                                                </Typography>
                                            )}
                                        </Box>


                                        <div style={{ display: 'flex', justifyContent: 'center' }}>
                                            <Button onClick={(event) => handleStartGame(event)}>
                                                <Wheel
                                                    mustStartSpinning={mustSpin}
                                                    prizeNumber={prizeNumber}
                                                    data={spinPlayer}
                                                    outerBorderColor="white"
                                                    innerBorderColor="white"
                                                    radiusLineColor="white"
                                                    innerRadius="10"
                                                    onStopSpinning={handleStopSpinning}
                                                />
                                            </Button>
                                        </div>

                                    </CardContent>
                                </Card>
                            </Modal>
                        )}
                        <div>
                            {players.map((player, index) => (
                                <div key={index} style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                                    <p>{player.name} ({player.gender})</p>
                                    <Button
                                        onClick={() => handleDeletePlayer(index)}
                                        variant="contained"
                                        color="secondary"
                                        startIcon={<DeleteIcon />}
                                    >
                                        Delete
                                    </Button>
                                </div>
                            ))}
                        </div>
                    </CardContent>
                </Card>
            </div>
        </div>

    );
};

export default PlayerList;                                                    